import type { ProfileDetail, ProfileParams } from '@/types/member'
import { http, http2 } from '@/utils/http'

/**
 * 获取个人信息
 */
export const getMemberProfileAPI = () => {
    return http2<ProfileDetail>({
        method: 'GET',
        url: '/user/user',
    })
}

// 修改个人信息
export const putMemberProfileAPI = (data: ProfileParams) => {
    return http2({
        method: 'PUT',
        url: '/user/user',
        data,
    })
}
